<template>
  <div class="poseton_mains">
    <Navbar></Navbar>
    <section>
      <div class="main_container" v-if="$store.state.abouts">
        <div class="row">
          <div class="col-xl-4">
            <div class="c_father">
              <div>
                <div class="text-white">
                </div>
                <div class="c_title" v-if="$store.state.language.lang"> {{$store.state.language.lang[32]}}</div>
                <div class="c_description">
                  {{$store.state.abouts[0].phone_number}}</div>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <div class="c_father">
              <div>
                <div>

                </div>
                <div class="c_title" v-if="$store.state.language.lang">
                  {{$store.state.language.lang[33]}}</div>
                <div class="c_description">
                  {{$store.state.abouts[0].address}}</div>
              </div>
            </div>
          </div>
          <div class="col-xl-4">
            <div class="c_father">
              <div>
                <div>

                </div>
                <div class="c_title" v-if="$store.state.language.lang">{{$store.state.language.lang[34]}}</div>
                <div class="c_description">
                  {{$store.state.abouts[0].email}}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="contact_pages">
            <div class="row">
              <div class="col-xl-6 col-12">
                <div class="maps ">
                  <l-map style="height: 700px;width: 100%" :zoom="zoom" :center="[$store.state.abouts[0].longitude,$store.state.abouts[0].latitude]">
                    <l-tile-layer :url="url"></l-tile-layer>

                    <l-marker  :draggable="false"  :lat-lng="[$store.state.abouts[0].longitude,$store.state.abouts[0].latitude]"
                    >
                      <l-icon
                          :icon-size="[40,40]"

                          icon-url="http://icons.iconarchive.com/icons/paomedia/small-n-flat/1024/map-marker-icon.png"
                      />

                    </l-marker>



                  </l-map>

                </div>
              </div>
              <div class="col-xl-6 col-12">
                <div class="contact_texts">
                  <div>
                    <div class="contact_text" v-if="$store.state.language.lang">  {{$store.state.language.lang[20]}}</div>
                    <div class="form_group">
                      <form @submit.prevent="submit()">
                        <labeL v-if="$store.state.language.lang">  {{$store.state.language.lang[21]}}</labeL>
                        <div class="form_f">
                          <input type="text" v-model="full_name">
                        </div>
                        <labeL v-if="$store.state.language.lang">  {{$store.state.language.lang[22]}}</labeL>
                        <div class="form_f">
                          <input type="text" placeholder="+998" v-model="phone_number">
                        </div>

                        <div class="form_b" v-if="$store.state.language.lang">
                          <button>{{$store.state.language.lang[29]}}</button>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
        </div>
      </div>
    </section>
    <Footer></Footer>
  </div>
</template>
<script>
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import Navbar from '../components/Navbar'
import {LMap, LTileLayer, LMarker,LIcon} from 'vue2-leaflet';
import Footer from "../components/Footer";
export default {
  name: 'MyComponent',
  components: {
    Footer,
    Navbar,
    LMap,
    LTileLayer,
    LMarker,
    LIcon},
  data() {
    return {
      url: 'http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
      zoom: 16,
      center: [  41.3340352,69.267303],
      markerLatLng:  [  41.3340352,69.267303],
      lat: "",
      lng: "",
      full_name:'',
      phone_number:'',
      settings: {
        "dots": false,
        "fade": true,
        "infinite": true,
        "speed": 500,
        "slidesToShow": 1,
        "slidesToScroll": 1
      },

      settings1: {
        "dots": false,
        "focusOnSelect": false,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 4,
        "slidesToScroll": 3,
        "touchThreshold": 5
      },
      settings2: {
        "dots": false,
        "focusOnSelect": false,
        "infinite": false,
        "speed": 500,
        "slidesToShow": 5,
        "slidesToScroll": 3,
        "touchThreshold": 5
      }
    }
  },
  mounted() {
    this.$store.dispatch('about')
  },
  methods: {
    prev() {
      this.$refs.carousel.prev()
    },
    next() {
      this.$refs.carousel.next()
    },

    submit(){
      this.$http.post("/api/appeal/",{
        "full_name":this.full_name,
        "phone_number":this.phone_number
      })
          .then(()=>{
            this.$router.go()
          })
    }
  }

}
</script>
